import {uniSwipeAction} from "@dcloudio/uni-ui/lib/uni-swipe-action/uni-swipe-action.vue";
import qqMap from '../../static/js/qqmap-wx-jssdk.js';
const qqmapsdk = new qqMap({
        key: 'N6JBZ-PVUCV-KJVPE-UYY2R-LZDHZ-DBFKL'
      });
import vuex from '../../static/js/store.js';
let region;
//初始化数据库
let listdata;
wx.cloud.init();
const db = wx.cloud.database({});
const linelist = db.collection('linelist');
export default {
	components:{
		uniSwipeAction
	},
    data(){
        return{
            suggestion:[],
            value:'',
            pages:1,
            checkpoint:[],
			options: [{
                text: '取消',
                style: {
                    backgroundColor: '#007aff'
                }
            }, {
                text: '删除',
                style: {
                    backgroundColor: '#dd524d'
                }
            }],
			mobileLocation : {//移动选择位置数据
			   title: '',
			   address:'',
			   category:'地名地址:行政地名',
			   location:{
				   lat:'',
				   lng:''
			   }
			}
        }
    },
    onLoad(options){
        region = options.city;
        this.value = options.value;
    },
    onUnload(){
        Object.assign(this, this.$options.data())
    },
    onShow(){
        this.getlinelist();
    },
    methods:{
		//移动选点
		moveToLocation: function () {
		   wx.chooseLocation({
		      success: res => {
				 let mobileLocation = {//移动选择位置数据
									   title: res.name,
									   address:res.address,
									   category:'地名地址:行政地名',
									   location:{
										   lat:res.latitude,
										   lng:res.longitude
									   }
									};
		         this.mobileLocation = mobileLocation
				 this.backfill(mobileLocation)
		      },
		      fail: function (err) {
		         console.log(err)
		      }
		   });
		},
		bindClick(ev,id){
			this.checkpoint.filter(o=>o._id!=id)
			// return
			if(ev.text == '删除'){
				this.deletedone(id)
			}
			else return;
		},
        getlinelist(){
            linelist.get({
                success:res=>{
                    this.checkpoint = res.data;
                }
            })
        },
        getsuggest(index){
            if(this.value==''){
                this.suggestion = [];
                return;
            }
            //调用关键词提示接口
            qqmapsdk.getSuggestion({
                keyword:this.value, //用户输入的关键词，可设置固定值,如keyword:'KFC'
                region:region, //设置城市名，限制关键词所示的地域范围，非必填参数
                page_index:index,
                page_size:10,
                success: res => {//搜索成功后的回调
                    this.suggestion.push(...res.data)
                },
                fail: function(error) {
                    console.error(error);
                }
            });
        },
        //数据回填方法
        backfill(item) {
            this.value = item.title;
            //储存搜索历史记录
            listdata = {address:item.address,category:item.category,location:item.location,title:item.title};
            linelist.get({
                success:res=>{
                    if(!res.data.map(o=>o.data).some(o=>o.address==listdata.address && o.title==listdata.title)){
                        linelist.add({
                            data:{
                                data:listdata
                            },
                            success: function(res) {
                                console.log(res._id)
                            }
                        })
                    }
                }
            })
            if(!item.category.includes('公交线路')){
                vuex.state.choosepoint = item;
                wx.navigateBack({
                    delta:1,
                })
            }
        },
        // 删除某一个历史数据
        deletedone(id){
            linelist.doc(id).remove({
                success: (res) => {
                    this.getlinelist();
                }
            })
        }
    }
}
